import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { HttpClient, HttpHeaders, HttpErrorResponse,HttpParams } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class DataService {

  constructor(private router: Router,
    private http: HttpClient) { }

  basePath = "http://localhost:5000/api/v1/"
  httpOptions = {
    headers: new HttpHeaders({
      'Content-Type': 'application/json',
      // "Access-Control-Allow-Origin":"*",
      // "Access-Control-Allow-Methods":"POST, GET, OPTIONS, DELETE, PUT",
      // "Access-Control-Max-Age":"1000",
      // "Access-Control-Allow-Headers":"x-requested-with, Content-Type, origin, authorization, accept, client-security-token"
    })
  };

  getstate(){
    return this.http.get<[]>(this.basePath + 'state',{
      headers:{['Content-Type']: 'application/json'},
    });
  }

  getcity(){
    return this.http.get<[]>(this.basePath + 'city',{
      headers:{['Content-Type']: 'application/json'},
    });
  }

  addcity(city, state){
    return this.http.post<[]>(this.basePath + 'city', {"cityname":city,"statename":state},this.httpOptions);
  }

  fareconfig(scity, dcity, routename, distance, bustype, rate, fare){
    return this.http.post<[]>(this.basePath + 'fareconfig', {"sourcecity":scity,"destinationcity":dcity, "routename":routename, "distance":distance, "bustype":bustype, "rate":rate, "fare":fare},this.httpOptions);
  }
}
