import { Component, OnInit } from '@angular/core';
import { DataService } from '../data.service';
import Swal from 'sweetalert2';
// import { format } from 'path';

@Component({
  selector: 'app-fareconfig',
  templateUrl: './fareconfig.component.html',
  styleUrls: ['./fareconfig.component.css']
})
export class FareconfigComponent implements OnInit {

  sourcecity  = 'select';
  destinationcity  = 'select';
  bustype  = 'select';
  city$;
  data = '';
  fare;
  rate;
  route;
  distance;
  order: string = 'cityname';
  constructor(private dataService: DataService) { }

  ngOnInit(): void {
    this.dataService.getcity().subscribe((data:any)=>{
      this.city$ = (data.data);
      console.log(this.city$);
      },
      error =>{
        console.log(error);
      });
    // });
    let number1 = (<HTMLInputElement>document.getElementById('exampleFormControlInput2'));
    number1.onkeyup = function myFunction() {
      number1.value = number1.value.replace(/[^0-9]/g, '');
  };

  let number2 = (<HTMLInputElement>document.getElementById('exampleFormControlInput4'));
    number2.onkeyup = function myFunction() {
      number2.value = number2.value.replace(/[^0-9]/g, '');
  };

  let number3 = (<HTMLInputElement>document.getElementById('exampleFormControlInput5'));
    number3.onkeyup = function myFunction() {
      number3.value = number3.value.replace(/[^0-9]/g, '');
  };
  }

  public inputValidator(event: any) {
    //console.log(event.target.value);
    const pattern = /^[a-zA-Z0-9]*$/;   
    //let inputChar = String.fromCharCode(event.charCode)
    if (!pattern.test(event.target.value)) {
      event.target.value = event.target.value.replace(/[^a-zA-Z0-9]/g, "");
      // invalid character, prevent input

    }
  }

  cities(event){
    this.data = event;
  }

  fareconfig(){
    this.dataService.fareconfig(this.sourcecity,this.destinationcity,this.route,this.distance,this.bustype,this.rate,this.fare).subscribe((data:any)=>{
      this.sourcecity = 'select';
      this.destinationcity = 'select';
      this.route = '';
      this.distance = '';
      this.bustype = 'select';
      this.rate = '';
      this.fare = '';
      Swal.fire('Success!','FareConfig Added Sucessfully !', 'success');
      },
      error =>{
        console.log(error);
        Swal.fire('Error!', error.error.error, 'error');
      });
  }

}
